import Vue from 'vue'
import VueRouter from 'vue-router'
import { Dialog } from 'vant'
import store from "@/store/"
Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    /*
    import Login from '@/views/login' ‰º†ÁªüÂÜôÊ≥ïüëáÊáíÂä†ËΩΩÂÜôÊ≥ï
    const Login = () => import('@/views/login')
    ÂçáÁ∫ßË∑ØÁî±ÊáíÂä†ËΩΩ üëá ÁöÑÂÜôÊ≥ï
    */
    component: () => import('@/views/login'),
    // https://router.vuejs.org/zh/guide/advanced/meta.html   requiresAuth Ëá™ÂÆö‰πâÁöÑÂêçÂ≠ó
    meta: { requiresAuth: false }

  },
  {
    path: '/',
    component: () => import('@/views/layout'),
    children: [
      {
        path: '', // Á©∫ÁùÄË°®Á§∫ÈªòËÆ§Â≠êË∑ØÁî±
        name: 'home',
        component: () => import('@/views/home'),
        meta: { requiresAuth: false }
      },
      {
        path: '/qa', // Á©∫ÁùÄË°®Á§∫ÈªòËÆ§Â≠êË∑ØÁî±
        name: 'qa',
        component: () => import('@/views/qa'),
        meta: { requiresAuth: false }
      },
      {
        path: '/video', // Á©∫ÁùÄË°®Á§∫ÈªòËÆ§Â≠êË∑ØÁî±
        name: 'video',
        component: () => import('@/views/video'),
        meta: { requiresAuth: false }
      },
      {
        path: '/my', // Á©∫ÁùÄË°®Á§∫ÈªòËÆ§Â≠êË∑ØÁî±
        name: 'my',
        component: () => import('@/views/my'),
        meta: { requiresAuth: false }
      }
    ]
  },
  {
    path: '/search',
    name: '/search',
    component: () => import('@/views/search'),

    meta: { requiresAuth: false }
  },
  {
    // Âä®ÊÄÅË∑ØÁî± : ÂÜíÂè∑
    path: '/article/:articleId',
    name: 'article',
    component: () => import('@/views/article'),
    // Â∞ÜÂä®ÊÄÅË∑ØÁî±ÁöÑÂèÇÊï∞ Êò†Â∞ÑÂà∞ÁªÑ‰ª∂ÁöÑprops‰∏≠
    props: true,
    meta: { requiresAuth: false }
  },
  {
    path: '/user/profile',
    name: 'userProfile',
    component: () => import("@/views/user-profile"),
    meta: { requiresAuth: false }
  }, {
    path: '/user/chat',
    name: 'userChat',
    component: () => import("@/views/user-chat"),
    meta: { requiresAuth: true }
  }
]

const router = new VueRouter({
  routes
})

// ÂÖ®Â±ÄÂâçÁΩÆÂÆàÂç´ to:Ë¶ÅËÆøÈóÆÁöÑÈ°µÈù¢Ë∑ØÁî±‰ø°ÊÅØ from:Êù•Ëá™Âì™‰∏™È°µÈù¢ÁöÑË∑ØÁî±‰ø°ÊÅØ next:ÊîæË°åÁöÑÊ†áËÆ∞

router.beforeEach((to, from, next) => {
  // Â¶ÇÊûú Ë∑ØÁî±ÂÖÉ‰ø°ÊÅØ‰∏≠ ÂÄº‰∏∫true ÊòØÊàë‰ª¨Ëá™Â∑±ËÆæÂÆöÁöÑÔºåÁî®Êù•Âå∫ÂàÜ‰∏çÂêåË∑ØÁî±Âú∞ÂùÄÊòØÂê¶ÈúÄË¶ÅËøõË°åÊüêÁßçÊìç‰ΩúÁöÑ‰∏ÄÁßç Ê†áËØÜ
  if (to.meta.requiresAuth) {
    if (store.state.user) {
      return next()
    }
    // Ë∞ÉÁî®‰∏Ä‰∏™ vant ÁªÑ‰ª∂ÁöÑÂºπÂá∫Ê°ÜÊèêÁ§∫
    Dialog.confirm({
      title: 'ËÆøÈóÆÊèêÁ§∫',
      message: 'ËØ•ÂäüËÉΩÈúÄË¶ÅÁôªÂΩïÂêéÊâçÂèØ‰ª•ËÆøÈóÆÔºåËØ∑ÈóÆÁôªÂΩïÂêóÔºü'
    }).then(() => {
      // Á°ÆËÆ§Áõ¥Êé•ËøáÂéª
      router.replace({
        name: 'login',
        query: {
          redirect: router.currentRoute.fullPath
        }
      })
    }).catch(() => {
      // ÂèñÊ∂àÊâßË°å https://router.vuejs.org/zh/guide/advanced/navigation-guards.html#%E5%85%A8%E5%B1%80%E5%89%8D%E7%BD%AE%E5%AE%88%E5%8D%AB
      next(false)
    });
  } else {
    // ‰∏çÈúÄË¶ÅÁôªÂΩïÁä∂ÊÄÅÁöÑÈ°µÈù¢ Áõ¥Êé•ËøáÂéª
    next()
  }
})

export default router
