import Vue from "vue"
// ÂàùÂßãÂåñ dayjs Áõ∏ÂÖ≥ÈÖçÁΩÆ
import dayjs from 'dayjs'
// ÂºïÂÖ•‰∏≠ÊñáËØ≠Ë®ÄÂåÖ
import 'dayjs/locale/zh-cn'
// ÂºïÂÖ•‰ΩøÁî®Áõ∏ÂØπ‰∫ã‰ª∂
import relativeTime from 'dayjs/plugin/relativeTime'

// ÈÖçÁΩÆ‰ΩøÁî®‰∏≠ÊñáËØ≠Ë®ÄÂåÖ
dayjs.locale('zh-cn')

dayjs.extend(relativeTime)

// Vue ËøáÊª§Âô®ÂÖ∂ÂÆûÂ∞±ÊòØ‰∏Ä‰∏™ÂèØ‰ª•Âú® Ê®°Êùø‰∏≠Ë∞ÉÁî®ÁöÑÂáΩÊï∞ËÄåÂ∑≤
// ÁªÑ‰ª∂‰∏≠ ‰ΩøÁî® {{ message | capitalize }}  Êàñ  v-bind:id="rawId | formatId"
Vue.filter('relativeTime', value => {
  return dayjs(value).to(dayjs())
})

Vue.filter('datetime', (value, format = 'YYYY-MM-DD HH:mm:ss') => {
  return dayjs(value).format(format)
})