import Vue from 'vue'
import Vuex from 'vuex'
import { getItem, setItem } from '@/utils/storage'
Vue.use(Vuex)

const USER_KEY = 'xinwenUser'
export default new Vuex.Store({
  state: {
    user: getItem(USER_KEY), // ÂΩìÂâçÁî®Êà∑ÁöÑÁôªÂΩïÁä∂ÊÄÅ TokenÁ≠âÊï∞ÊçÆ
    cachePages: ['layoutIndex']
  },
  mutations: {
    // ‰∏∫‰∫ÜÈò≤Ê≠¢È°µÈù¢Âà∑Êñ∞Êï∞ÊçÆ‰∏¢Â§±ÔºåÈúÄË¶ÅÊääÊï∞ÊçÆÊîæÂà∞Êú¨Âú∞‰∏≠Â≠òÂÇ®Ôºå‰∏îÊú¨Âú∞‰∏≠Âè™Â≠ò Â≠óÁ¨¶‰∏≤
    setUser(state, data) {
      state.user = data
      setItem(USER_KEY, state.user)
    },
    // Ê∑ªÂä†ÁºìÂ≠òÈ°µÈù¢
    addCachePages(state, pageName) {
      if (!state.cachePages.includes(pageName)) {
        state.cachePages.push(pageName)
      }
    },
    // ÁßªÈô§ÁºìÂ≠òÈ°µÈù¢
    removeCachePages(state, pageName) {
      const index = state.cachePages.indexOf(pageName)
      if (index !== -1) {
        state.cachePages.splice(index, 1)
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
